import React, { Fragment, Component } from "react";

import { Avatar, Progress } from "antd";

import { Col, Row } from "reactstrap";

import * as common from "../../../Utilities/constants";
class NewAccountCreated extends Component {
  constructor(props) {
    super(props);

    this.state = {
      percent: 0,
    };

    this.interval = null;
  }

  componentDidMount() {
    this.interval = setInterval(() => {
      this.setState((prevState) => {
        if (prevState.percent >= 100) {
          clearInterval(this.interval);
          this.props.history.push("/home");
          return { percent: 100 };
        }
        return { percent: prevState.percent + 100 / (60 * 10) };
      });
    }, 100);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  render() {
    return (
      <Fragment>
        <div className="h-100">
          <Row
            className="h-100 g-0"
            style={{ display: "flex", justifyContent: "center" }}
          >
            <Col
              xs={11}
              sm={10}
              md={8}
              xl={7}
              xxl={6}
              style={{
                marginTop: "104px",
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
              }}
            >
              <div
                style={{
                  fontSize: "20px",
                  fontWeight: "bold",
                  textAlign: "center",
                }}
              >
                One minute, your site is being created
              </div>
              <div style={{ marginTop: "40px", display: "flex", gap: "16px" }}>
                <Avatar
                  style={{
                    width: "160px",
                    minWidth: "160px",
                    height: "160px",
                    minHeight: "160px",
                  }}
                  src={common.DOJO_ICON}
                ></Avatar>
                <div
                  style={{
                    display: "flex",
                    flexDirection: "column",
                    gap: "16px",
                  }}
                >
                  <div style={{ fontWeight: "500" }}>
                    While you waiting, meet Dojo, the intellaphant in the room.
                  </div>
                  <div style={{ color: "gray" }}>
                    Dojo is an AI assistant that can help you in your pursuit of
                    transforming your organization.
                  </div>
                  <div style={{ color: "gray" }}>
                    While you wait, read Dojos advice on transforming your
                    business:
                  </div>
                  <div style={{ color: "gray", fontStyle: "italic" }}>
                    Hi, I'm Dojo. Your Intelliphant in the room. I play a
                    pivotal role in bolstering your business agility and
                    decision advantage strategies. By harnessing the power of AI
                    in me, your organizations can swiftly adapt to changing
                    market conditions, make data-driven decisions, and gain a
                    competitive edge.
                  </div>
                </div>
              </div>
              <Progress
                style={{ width: "75%", marginTop: "32px" }}
                percent={this.state.percent}
                showInfo={false}
                strokeColor="rgba(0, 161, 8, 0.86)"
              />
              <div
                style={{
                  textAlign: "center",
                }}
              >
                Thanks for signing up! Dojo is working on your Pivitle Cloud
                site.
              </div>
              <div
                style={{
                  textAlign: "center",
                }}
              >
                This wont take more than a minute or two. Youll be taken there
                once its ready.
              </div>
            </Col>
          </Row>
        </div>
      </Fragment>
    );
  }
}

export default NewAccountCreated;
