import React, { Fragment, Component } from "react";
import axios from "axios";
import { baseURL } from "../../../config/variables";
import { toast } from "react-toastify";

import {
  Col,
  Row,
  Button,
  Form,
  FormGroup,
  FormFeedback,
  Label,
  Input,
} from "reactstrap";

class UpdateUser extends Component {
  constructor(props) {
    super(props);

    this.state = {
      formData: {
        firstName: "",
        lastName: "",
        password: "",
        passwordrep: "",
      },
      formErrors: {
        firstName: "",
        lastName: "",
        password: "",
        passwordrep: "",
      },
    };
  }

  componentDidMount() {
    const { location } = this.props;
    const queryParams = new URLSearchParams(location.search);

    localStorage.setItem("Authorization", queryParams.get("token"));
  }

  render() {
    const handleChange = (e) => {
      const { name, value } = e.target;
      this.setState({
        ...this.state,
        formData: {
          ...this.state.formData,
          [name]: value,
        },
      });
    };

    const handleSubmit = async (e) => {
      e.preventDefault();

      const errors = validateForm(this.state.formData);
      if (Object.keys(errors).length === 0) {
        try {
          this.setState({
            formErrors: {
              firstName: "",
              lastName: "",
              password: "",
              passwordrep: "",
            },
          });

          const response = await axios.put(
            baseURL + "/user",
            {
              firstName: this.state.formData.firstName,
              lastName: this.state.formData.lastName,
              password: this.state.formData.password,
            },
            {
              headers: {
                Authorization: `Bearer ${localStorage.getItem(
                  "Authorization"
                )}`,
              },
            }
          );

          localStorage.setItem("user", JSON.stringify(response.data.data));

          this.props.history.push("/home");

          this.setState({
            formData: {
              firstName: "",
              lastName: "",
              password: "",
              passwordrep: "",
            },
          });
        } catch (err) {
          toast.error(err.response.data.message);
          this.setState({
            ...this.state,
            formErrors: {
              ...err,
            },
          });
        }
      } else {
        this.setState({
          ...this.state,
          formErrors: {
            ...errors,
          },
        });
      }
    };

    const validateForm = (data) => {
      let errors = {};
      if (!data.firstName.trim()) {
        errors.firstName = "First Name is required.";
      }
      if (!data.lastName.trim()) {
        errors.lastName = "Last Name is required.";
      }
      if (!data.password) {
        errors.password = "Password is required.";
      }
      if (!data.passwordrep) {
        errors.passwordrep = "Please re-enter password.";
      } else if (data.password !== data.passwordrep) {
        errors.passwordrep = "Passwords must match.";
      }
      return errors;
    };

    return (
      <Fragment>
        <div className="h-100">
          <Row
            className="h-100 g-0"
            style={{ display: "flex", justifyContent: "center" }}
          >
            <Col
              xs={10}
              sm={8}
              md={6}
              lg={5}
              xl={4}
              xxl={3}
              style={{
                marginTop: "104px",
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
              }}
            >
              <div
                style={{
                  width: "100%",
                  border: "1px solid #bbbbbb",
                  borderRadius: "12px",
                  padding: "32px 32px 48px",
                  backgroundColor: "white",
                }}
              >
                <div
                  style={{
                    textAlign: "center",
                    color: "#0182e1",
                    fontSize: "28px",
                    fontWeight: "bold",
                  }}
                >
                  Pivitle
                </div>
                <div
                  style={{
                    marginTop: "8px",
                    textAlign: "center",
                    fontSize: "18px",
                  }}
                >
                  Email address verification
                </div>
                <div style={{ textAlign: "center", fontSize: "14px" }}>
                  Finish setting up your account
                </div>
                <Form
                  onSubmit={handleSubmit}
                  style={{
                    marginTop: "8px",
                  }}
                >
                  <FormGroup>
                    <Label for="exampleName" style={{ fontSize: "14px" }}>
                      <span className="text-danger">*</span> First Name
                    </Label>
                    <Input
                      type="text"
                      name="firstName"
                      id="firstName"
                      placeholder="First Name here..."
                      value={this.state.formData.firstName}
                      onChange={handleChange}
                      invalid={!!this.state.formErrors.firstName}
                    />
                    {this.state.formErrors.firstName && (
                      <FormFeedback>
                        {this.state.formErrors.firstName}
                      </FormFeedback>
                    )}
                  </FormGroup>
                  <FormGroup>
                    <Label for="exampleName" style={{ fontSize: "14px" }}>
                      <span className="text-danger">*</span> Last Name
                    </Label>
                    <Input
                      type="text"
                      name="lastName"
                      id="lastName"
                      placeholder="Last Name here..."
                      value={this.state.formData.lastName}
                      onChange={handleChange}
                      invalid={!!this.state.formErrors.lastName}
                    />
                    {this.state.formErrors.lastName && (
                      <FormFeedback>
                        {this.state.formErrors.lastName}
                      </FormFeedback>
                    )}
                  </FormGroup>
                  <FormGroup>
                    <Label for="examplePassword" style={{ fontSize: "14px" }}>
                      <span className="text-danger">*</span> Password
                    </Label>
                    <Input
                      type="password"
                      name="password"
                      id="examplePassword"
                      placeholder="Password here..."
                      value={this.state.formData.password}
                      onChange={handleChange}
                      invalid={!!this.state.formErrors.password}
                    />
                    {this.state.formErrors.password && (
                      <FormFeedback>
                        {this.state.formErrors.password}
                      </FormFeedback>
                    )}
                  </FormGroup>
                  <FormGroup>
                    <Label
                      for="examplePasswordRep"
                      style={{ fontSize: "14px" }}
                    >
                      <span className="text-danger">*</span> Repeat Password
                    </Label>
                    <Input
                      type="password"
                      name="passwordrep"
                      id="examplePasswordRep"
                      placeholder="Repeat Password here..."
                      value={this.state.formData.passwordrep}
                      onChange={handleChange}
                      invalid={!!this.state.formErrors.passwordrep}
                    />
                    {this.state.formErrors.passwordrep && (
                      <FormFeedback>
                        {this.state.formErrors.passwordrep}
                      </FormFeedback>
                    )}
                  </FormGroup>
                  <div
                    style={{
                      textAlign: "center",
                      paddingInline: "8px",
                      fontSize: "12px",
                    }}
                  >
                    By signing up, I accept the Atlassian Cloud Terms of Service
                    and acknowledge the Privacy Policy.
                  </div>
                  <Button
                    style={{
                      width: "100%",
                      marginTop: "8px",
                      backgroundColor: "#0182e1",
                      borderColor: "#0182e1",
                    }}
                    color="primary"
                    size="sm"
                    type="submit"
                  >
                    Confirm
                  </Button>
                </Form>
              </div>
            </Col>
          </Row>
        </div>
      </Fragment>
    );
  }
}

export default UpdateUser;
